import {
  ArcElement,
  BarController,
  BarElement,
  CategoryScale,
  Chart,
  ChartOptions,
  Filler,
  Legend,
  LinearScale,
  LineController,
  LineElement,
  PointElement,
  PolarAreaController,
  RadialLinearScale,
  Tooltip,
} from 'chart.js';
import {useEffect, useRef} from 'react';
import {BaseChartProps} from './base-chart';

Chart.register([
  LineElement,
  PointElement,
  BarElement,
  ArcElement,
  LineController,
  BarController,
  PolarAreaController,
  RadialLinearScale,
  CategoryScale,
  LinearScale,
  Tooltip,
  Filler,
  Legend,
]);

export default function LazyChart({
  type,
  data,
  options,
  hideLegend,
}: Omit<BaseChartProps<any>, 'children'>) {
  const ref = useRef<HTMLCanvasElement>(null);
  const chartRef = useRef<Chart>();

  useEffect(() => {
    if (ref.current) {
      chartRef.current = new Chart(ref.current, {
        type,
        data,
        options: {
          maintainAspectRatio: false,
          animation: {
            duration: 250,
          },
          plugins: {
            legend: {
              position: 'bottom',
              display: !hideLegend,
            },
          },
          ...(options as ChartOptions),
        },
      });
    }

    return () => {
      chartRef.current?.destroy();
    };
  }, [data, type, options]);

  return <canvas ref={ref}></canvas>;
}
